import React from "react";
import { createUser } from "../../DataManager/config";

const FormContainer = ({ namePlaceHolder, messagePlaceholder }) => {
  const handleSubmit = async e => {
    e.preventDefault();
    const form = e.target;
    const name = form.elements.name.value;
    const email = form.elements.email.value;
    const message = form.elements.textareaMessage.value;
    const validEmail = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email);
    if (!validEmail) {
      return window.alert("Please input correct Email!");
    }
    await createUser(name, email, message);
    form.elements.name.value = "";
    form.elements.email.value = "";
    form.elements.textareaMessage.value = "";
  };
  return (
    <>
      <div className="one_half last_column">
        <div className="main-contactform">
          <div
            role="form"
            className="wpcf7"
            id="wpcf7-f82-p43-o1"
            lang="en-US"
            dir="ltr"
          >
            <div className="screen-reader-response" />
            <form
              className="wpcf7-form"
              noValidate="novalidate"
              onSubmit={handleSubmit}
            >
              <div className="form-group">
                <span className="wpcf7-form-control-wrap text-397">
                  <input
                    type="text"
                    name="name"
                    size="40"
                    className="wpcf7-form-control wpcf7-text wpcf7-validates-as-required form-control"
                    aria-required="true"
                    aria-invalid="false"
                    placeholder={namePlaceHolder}
                  />
                </span>
              </div>

              <div className="form-group">
                <span className="wpcf7-form-control-wrap email-902">
                  <input
                    type="email"
                    name="email"
                    size="40"
                    className="wpcf7-form-control wpcf7-text wpcf7-email wpcf7-validates-as-required wpcf7-validates-as-email form-control"
                    aria-required="true"
                    aria-invalid="false"
                    placeholder="Email Address"
                  />
                </span>
              </div>

              <div className="form-group">
                <span className="wpcf7-form-control-wrap textarea-609">
                  <textarea
                    name="textareaMessage"
                    cols="40"
                    rows="10"
                    className="wpcf7-form-control wpcf7-textarea"
                    aria-invalid="false"
                    placeholder={messagePlaceholder}
                  />
                </span>
              </div>

              <div className="form-group">
                <input
                  type="submit"
                  className="wpcf7-form-control wpcf7-submit send-btn"
                />
                <span className="ajax-loader" />
              </div>
              <div className="wpcf7-response-output wpcf7-display-none" />
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default FormContainer;
