import React, { useEffect, useState } from "react";
import { getContact, getMenu } from "../../DataManager/config";
import InfoContainer from "./InfoContainer";
import LinksContainer from "./LinksContainer";
import FollowContainer from "./FollowContainer";
import BottomContainer from "./BottomContainer";

import { connect } from "react-redux";

const Footer = ({ transparent, dispatch, lang }) => {
  const [contact, setContact] = useState("Travern");
  const [menu, setMenu] = useState("Travern");
  const usefulLink = lang === "vn" ? "ng Dn" : "Useful Links";
  useEffect(() => {
    getMenu().then(item => {
      if (!item) {
        return;
      }
      const data = item.data();
      setMenu(data);
    });
  }, []);

  useEffect(() => {
    getContact().then(item => {
      if (!item) {
        return;
      }
      const data = item.data();
      setContact(data);
    });
  }, []);

  return (
    <div id="footer-wrapper">
      <div className="container">
        <InfoContainer
          title={contact && contact.about && contact.about[lang]}
          description={
            contact && contact.description && contact.description[lang]
          }
          resortAddress={
            contact && contact.resortAddress && contact.resortAddress[lang]
          }
          representativeAddress={
            contact &&
            contact.representativeAddress &&
            contact.representativeAddress[lang]
          }
          email={contact.email}
          phone={contact.resortPhone}
          representativePhone={
            contact &&
            contact.representativePhone &&
            contact.representativePhone[lang]
          }
        />
        <LinksContainer
          home={menu && menu.home && menu.home[lang]}
          rooms={menu && menu.room && menu.room[lang]}
          services={menu && menu.services && menu.services[lang]}
          packages={menu && menu.packages && menu.packages[lang]}
          gallery={menu && menu.gallery && menu.gallery[lang]}
          about={menu && menu.about && menu.about[lang]}
          contact={menu && menu.contact && menu.contact[lang]}
          usefulLink={usefulLink}
        />
        <FollowContainer
          newsletter={contact && contact.newsletter && contact.newsletter[lang]}
          follow={contact && contact.follow && contact.follow[lang]}
        />
        <div className="clear" />
      </div>
      <BottomContainer />
    </div>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(Footer);
