import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { setNewsDetail as setNewsDetailAction } from "../../redux/actions/news";

const HotNewsContainer = ({ hotItems, setNewsDetail, lang }) => {
  const setDetail = data => {
    setNewsDetail(data);
  };
  return (
    <>
      <aside id="recent-posts-2" className="widget widget_recent_entries">
        <div className="widget_content">
          {" "}
          <h3 className="widget-title">
            <span>Recent News</span>
          </h3>{" "}
          <ul>
            {hotItems &&
              hotItems.map((hotItem, index) => {
                return (
                  <li key={index}>
                    <Link
                      to={`/newsDetail/${hotItem.id}`}
                      onClick={() => setDetail(hotItem.id)}
                    >
                      {hotItem.name && hotItem.name[lang]}
                    </Link>
                  </li>
                );
              })}
          </ul>
        </div>
      </aside>
    </>
  );
};

export default connect(
  state => {
    return {
      lang: state.lang.currentLang
    };
  },
  dispatch => ({
    setNewsDetail: data => dispatch(setNewsDetailAction(data))
  })
)(HotNewsContainer);
