import React from "react";
import { connect } from "react-redux";
import { setRoomDetail as setRoomDetailAction } from "../../redux/actions/room";
import { Link } from "react-router-dom";
import Banner from "../Common/Banner";

const RoomsPage = ({ transparent, dispatch, lang, rooms, setRoomDetail }) => {
  const title = lang === "vn" ? "Phng MUINE BAY" : "OUR ROOMS";
  const setDetail = data => {
    setRoomDetail(data);
  };

  const priceSubtitle = lang === "vn" ? "Phng/m" : "From/Per Night";
  return (
    <div>
      <Banner name={title} />

      <div className="container content-area">
        <div className="middle-align">
          <div className="site-main sitefull">
            <article
              id="post-128"
              className="post-128 page type-page status-publish hentry"
            >
              <div className="entry-content">
                {rooms &&
                  rooms.map((room, index) => {
                    if (room.id === "common") {
                      return null;
                    }
                    const isLast = index !== 0 && index % 3 === 0;
                    return (
                      <div
                        key={index}
                        className={
                          isLast ? "one_third last_column" : "one_third"
                        }
                      >
                        <div className="hotel-room">
                          <div className="hotel-room-thumb">
                            <img
                              alt="MUINE BAY"
                              src={room && room.urls && room.urls[0]}
                            />
                            <div className="room-thumb-overlay">
                              <Link
                                to={`/rooms/${room.id}`}
                                onClick={() => setDetail(room.id)}
                              >
                                View More
                              </Link>
                            </div>
                          </div>
                          <div className="hotel-room-cont">
                            <div className="hotel-room-cont-wrap">
                              <Link
                                to={`/rooms/${room.id}`}
                                onClick={() => setDetail(room.id)}
                              >
                                <h3 className="yellow-text">
                                  {room.name && room.name[lang]}
                                </h3>
                              </Link>
                              <p>
                                {" "}
                                {room.short_description &&
                                  room.short_description[lang]}{" "}
                              </p>
                              <div className="hotel-room-price">
                                {room.price && room.price[lang]}
                                <span className="price-title">
                                  {priceSubtitle}
                                </span>{" "}
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="clear" />
                      </div>
                    );
                  })}

                <div className="clear" />
              </div>
            </article>{" "}
          </div>
          <div className="clear" />
        </div>
      </div>
    </div>
  );
};

export default connect(
  state => {
    return {
      lang: state.lang.currentLang,
      rooms: state.room.list
    };
  },
  dispatch => ({
    setRoomDetail: data => dispatch(setRoomDetailAction(data))
  })
)(RoomsPage);
