import React from "react";
import { connect } from "react-redux";

import Banner from "../Common/Banner";
import ThreeCollumnsContainer from "./ThreeCollumnsContainer";
import FourColumnsContainer from "./FourColumnsContainer";

const GalleryPage = ({ lang, rooms }) => {
  const bannerTitle = lang === "vn" ? "HNH NH" : "OUR GALLERY";
  let firstHalf = [];
  let secondHalf = [];
  rooms.forEach((room, index) => {
    const urls = room && room.urls;
    if (index < rooms.length / 2) {
      firstHalf = firstHalf.concat(urls ?? []);
    } else {
      secondHalf = secondHalf.concat(urls ?? []);
    }
  });
  return (
    <div>
      <Banner name={bannerTitle} />
      <div className="container content-area">
        <div className="middle-align">
          <div className="site-main sitefull">
            <article
              id="post-26"
              className="post-26 page type-page status-publish hentry"
            >
              <div className="entry-content">
                <ThreeCollumnsContainer
                  photos={firstHalf}
                  title="MUINE BAY RESORT"
                />
                <div className="clear" />
                <FourColumnsContainer photos={secondHalf} />
                <div className="clear" />
              </div>
            </article>{" "}
          </div>
          <div className="clear" />
        </div>
      </div>
    </div>
  );
};

export default connect(state => {
  return {
    rooms: state.room.list,
    lang: state.lang.currentLang
  };
})(GalleryPage);
