import React, { useEffect, useState } from "react";
import { getContact } from "../../DataManager/config";
import { connect } from "react-redux";
import Banner from "../Common/Banner";
import ContentContainer from "./ContentContainer";
import FormContainer from "./FormContainer";

const Contact = ({ transparent, dispatch, lang }) => {
  const [contact, setContact] = useState("Travern");

  useEffect(() => {
    getContact().then(item => {
      if (!item) {
        return;
      }
      const data = item.data();
      setContact(data);
    });
  }, []);

  return (
    <div>
      <Banner
        name={contact && contact.page_title && contact.page_title[lang]}
      />

      <div className="content-area">
        <div className="middle-align">
          <div className="site-main sitefull">
            <div className="container">
              <div className="contact-content">
                <ContentContainer
                  title={
                    contact &&
                    contact.contact_title &&
                    contact.contact_title[lang]
                  }
                  description={
                    contact && contact.description && contact.description[lang]
                  }
                  addressTitle={
                    contact &&
                    contact.address_title &&
                    contact.address_title[lang]
                  }
                  representativeAddress={
                    contact &&
                    contact.representativeAddress &&
                    contact.representativeAddress[lang]
                  }
                  email={contact.email}
                  phoneTitle={
                    contact && contact.phone_title && contact.phone_title[lang]
                  }
                  phone={contact.resortPhone}
                  fax={contact.fax}
                  resortAddress={
                    contact &&
                    contact.resortAddress &&
                    contact.resortAddress[lang]
                  }
                  addressOffice={
                    contact &&
                    contact.address_office &&
                    contact.address_office[lang]
                  }
                  saleEmail={contact.sale_email}
                  representativePhone={
                    contact &&
                    contact.representativePhone &&
                    contact.representativePhone[lang]
                  }
                />
                <FormContainer
                  namePlaceHolder={
                    contact &&
                    contact.name_placeholder &&
                    contact.name_placeholder[lang]
                  }
                  messagePlaceholder={
                    contact &&
                    contact.message_placeholder &&
                    contact.message_placeholder[lang]
                  }
                  sendInfo={
                    contact && contact.send_info && contact.send_info[lang]
                  }
                />
                <div className="clear" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="gmap">
        <iframe
          title="MuiNe Map"
          // className="blackmap"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3917.5527603587275!2d108.29021691479475!3d10.921554492223168!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31768e51a82cc973%3A0x6cdf011c87005b4f!2sMuine%20Bay%20Resort!5e0!3m2!1svi!2s!4v1581912284854!5m2!1svi!2s"
          width="98%"
          height="600"
          frameBorder="0"
        />
      </div>

      <div className="clear" />
    </div>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(Contact);
