import React from "react";
import { connect } from "react-redux";

const AmenitiesContainer = ({ lang, selectedRoom, roomInfo }) => {
  let howManyPeople = "";
  let roomSize = "";
  let hasBathtub = "";

  const longDescription =
    selectedRoom &&
    selectedRoom.long_description &&
    selectedRoom.long_description[lang];
  const oceanValue =
    roomInfo && roomInfo.oceanValue && roomInfo.oceanValue[lang];
  const gradenValue =
    roomInfo && roomInfo.gradenValue && roomInfo.gradenValue[lang];
  const roomView =
    selectedRoom.id === "ocean_panorama" ? oceanValue : gradenValue;

  switch (selectedRoom.id) {
    case "superior":
    case "deluxe":
      howManyPeople = "2";
      roomSize = "38 Sqm";
      hasBathtub = roomInfo && roomInfo.no && roomInfo.no[lang];
      break;
    case "ocean_panorama":
      howManyPeople = "2";
      roomSize = "51 Sqm";
      hasBathtub = roomInfo && roomInfo.yes && roomInfo.yes[lang];
      break;
    case "bungalow":
      howManyPeople = "2";
      roomSize = "62 Sqm";
      hasBathtub = roomInfo && roomInfo.yes && roomInfo.yes[lang];
      break;
    case "family":
      howManyPeople = "3";
      roomSize = "54 Sqm";
      hasBathtub = roomInfo && roomInfo.no && roomInfo.no[lang];
      break;
    case "family_suite":
      howManyPeople = "4";
      roomSize = "62 Sqm";
      hasBathtub = roomInfo && roomInfo.yes && roomInfo.yes[lang];
      break;
    default:
  }

  return (
    <>
      <div className="room-content">
        <div className="room-meta-info">
          <br />
          <h4>
            {roomInfo &&
              roomInfo.amenities_title &&
              roomInfo.amenities_title[lang]}
          </h4>
          <div className="inner">
            <ul className="opalhotel-room-meta">
              <li className="meta-bed">
                <span className="meta-label">
                  {roomInfo && roomInfo.bed && roomInfo.bed[lang]}
                </span>
                <span className="meta-text double-bed">
                  {selectedRoom &&
                    selectedRoom.bedType &&
                    selectedRoom.bedType[lang]}
                </span>
              </li>

              <li className="meta-adults">
                <span className="meta-label">
                  {roomInfo && roomInfo.people && roomInfo.people[lang]}
                </span>
                <span className="meta-text">{howManyPeople}</span>
              </li>

              <li className="meta-size">
                <span className="meta-label">
                  {roomInfo && roomInfo.roomSize && roomInfo.roomSize[lang]}
                </span>
                <span className="meta-text">{roomSize}</span>
              </li>

              <li className="meta-view">
                <span className="meta-label">
                  {roomInfo && roomInfo.view && roomInfo.view[lang]}
                </span>
                <span className="meta-text">{roomView}</span>
              </li>
              <li className="meta-wifi">
                <span className="meta-label">Wifi</span>
                <span className="meta-text">
                  {roomInfo && roomInfo.yes && roomInfo.yes[lang]}
                </span>
              </li>
              <li className="meta-bearkfast">
                <span className="meta-label">
                  {roomInfo && roomInfo.breakfast && roomInfo.breakfast[lang]}
                </span>
                <span className="meta-text">
                  {roomInfo && roomInfo.yes && roomInfo.yes[lang]}
                </span>
              </li>
              <li className="meta-cable_tv">
                <span className="meta-label">
                  {roomInfo && roomInfo.cable && roomInfo.cable[lang]}
                </span>
                <span className="meta-text">
                  {roomInfo && roomInfo.yes && roomInfo.yes[lang]}
                </span>
              </li>
              <li className="meta-pickup">
                <span className="meta-label">
                  {roomInfo && roomInfo.transit && roomInfo.transit[lang]}
                </span>
                <span className="meta-text">
                  {roomInfo && roomInfo.yes && roomInfo.yes[lang]}
                </span>
              </li>
              <li className="meta-freebathtub">
                <span className="meta-label">
                  {roomInfo && roomInfo.bathtub && roomInfo.bathtub[lang]}
                </span>
                <span className="meta-text">{hasBathtub}</span>
              </li>
              <li className="meta-petsallowed">
                <span className="meta-label">
                  {roomInfo && roomInfo.pet && roomInfo.pet[lang]}
                </span>
                <span className="meta-text">
                  {roomInfo && roomInfo.no && roomInfo.no[lang]}
                </span>
              </li>
              <li className="meta-roomservice">
                <span className="meta-label">
                  {roomInfo &&
                    roomInfo.roomService &&
                    roomInfo.roomService[lang]}
                </span>
                <span className="meta-text">
                  {roomInfo && roomInfo.yes && roomInfo.yes[lang]}
                </span>
              </li>

              <li className="meta-iron">
                <span className="meta-label">Iron</span>
                <span className="meta-text">
                  {roomInfo && roomInfo.no && roomInfo.no[lang]}
                </span>
              </li>
            </ul>
          </div>
        </div>
        <div className="room-description">
          <h4>
            {roomInfo && roomInfo.description && roomInfo.description[lang]}
          </h4>

          <div className="content">
            <div
              dangerouslySetInnerHTML={{
                __html: longDescription
              }}
            ></div>
          </div>
        </div>
      </div>
    </>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(AmenitiesContainer);
