import React from "react";
import { createNewsletter } from "../../DataManager/config";

const FollowContainer = ({ newsletter, follow }) => {
  const handleSubmit = async e => {
    e.preventDefault();
    const form = e.target;
    const email = form.elements.email.value;
    const validEmail = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email);
    if (!validEmail) {
      return window.alert("Please input correct Email!");
    }
    await createNewsletter(email);
    form.elements.email.value = "";
  };
  return (
    <>
      <div className="cols-3 widget-column">
        <h5>{newsletter}</h5>
        <div
          role="form"
          className="wpcf7"
          id="wpcf7-f109-o1"
          lang="en-US"
          dir="ltr"
        >
          <div className="screen-reader-response" />
          <form
            action="/travern/#wpcf7-f109-o1"
            method="post"
            className="wpcf7-form"
            noValidate="novalidate"
            onSubmit={handleSubmit}
          >
            <div className="newsletter">
              <span className="wpcf7-form-control-wrap nemail">
                <input
                  type="email"
                  name="email"
                  size="40"
                  className="wpcf7-form-control wpcf7-text wpcf7-email wpcf7-validates-as-required wpcf7-validates-as-email"
                  placeholder="Email Address"
                />
              </span>
              <button type="submit" className="hover-link">
                <i className="fa fa-paper-plane" />
              </button>
            </div>
            <div className="wpcf7-response-output wpcf7-display-none" />
          </form>
        </div>
        <h5>{follow}</h5>
        <div className="social-icons">
          <a
            style={{ backgroundColor: "" }}
            href="https://www.facebook.com/MuineBay.Resort.PhanThiet"
            target="_blank"
            rel="noopener noreferrer"
            title="facebook"
          >
            <i className="fab fa-facebook-f"></i>
          </a>

          <a
            style={{ backgroundColor: "" }}
            href="https://www.instagram.com/muinebay_resort"
            target="_blank"
            rel="noopener noreferrer"
            title="instagram"
          >
            <i className="fab fa-instagram"></i>
          </a>

          <a
            style={{ backgroundColor: "" }}
            href="https://www.youtube.com/channel/UCJHrfxgpXOERPIa7M9CL8Pg"
            target="_blank"
            rel="noopener noreferrer"
            title="youtube"
          >
            <i className="fab fa-youtube"></i>
          </a>
          <a
            style={{ backgroundColor: "" }}
            href="https://www.tripadvisor.com/Hotel_Review-g1009804-d2075996-Reviews-Muine_Bay_Resort-Mui_Ne_Phan_Thiet_Binh_Thuan_Province.html"
            target="_blank"
            rel="noopener noreferrer"
            title="tripadvisor"
          >
            <i className="fab fa-tripadvisor"></i>
          </a>
        </div>{" "}
      </div>
    </>
  );
};

export default FollowContainer;
