import React from "react";
import YouTube from "react-youtube";
import { throttle } from "lodash";
import { getMedia } from "../../DataManager/config";

export default class VideoBackground extends React.Component {
  state = {
    className: "home hide",
    videoID: null,
    videoHeight: "100%",
    player: null
  };

  constructor(props) {
    super(props);
    this.fetchData();
  }

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  }

  fetchData = () => {
    getMedia().then(item => {
      if (!item) {
        return;
      }
      const data = item.data();
      this.setState({ videoID: data && data.videoID });
    });
  };

  onReady = event => {
    event.target.playVideo();
  };

  handleResize = throttle(() => {
    this.setState({
      videoWidth: `${window.innerWidth}px`,
      videoHeight: `${(window.innerWidth * 9) / 16}px`
    });
  }, 100);

  render() {
    const opts = {
      height: (window.innerWidth * 9) / 16,
      width: window.innerWidth,
      playerVars: {
        autoplay: 1,
        rel: 0,
        autohide: 2,
        controls: 0,
        modestbranding: 1,
        disablekb: 1,
        iv_load_policy: 3,
        mute: 1,
        fs: 0,
        showinfo: 0,
        enablejsapi: 0,
        html5: 1,
        hd: 1,
        wmode: "opaque",
        playsinline: 1
      }
    };

    return (
      <div className="home-wrapper">
        <div className={this.state.className} style={{ overflow: "hidden" }}>
          <div
            className="youtube-wrapper"
            style={{
              height: this.state.videoHeight,
              width: this.state.videoWidth
            }}
          >
            <YouTube
              id="videoPlayer1"
              host="https://www.youtube.com"
              origin="http://pjmap.com:3000/"
              videoId={this.state.videoID}
              allowFullScreen
              opts={opts}
              onPlay={e => {
                this.setState({ className: "home" });
              }}
              onReady={this.onReady}
              onEnd={e => {
                e.target.playVideo();
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}
