import React from "react";
import { connect } from "react-redux";

const FourColumnsContainer = ({ photos, lang }) => {
  const subtitle = lang === "vn" ? "Hnh nh" : "Photos";
  return (
    <>
      <div style={{ height: "50px" }} />
      <div className="custom-heading" style={{ textAlign: "left" }}>
        <h2 style={{ color: "#242c3a" }}>
          <span>MUINE BAY RESORT</span>
          {subtitle}
        </h2>
      </div>

      {photos &&
        photos.map((photoUrl, index) => {
          const isLast = index !== 0 && (index + 1) % 4 === 0;
          const classNameGallery = isLast
            ? "gallery gal-col-four lastcols"
            : "gallery gal-col-four";
          return (
            <div className={classNameGallery}>
              <div index={index} key={index} className="gallery-image">
                <img alt="MUINE BAY" src={photoUrl} />
                <div className="gallery-icon">
                  <a
                    href={photoUrl}
                    data-fancybox="images"
                    className="yellow-text"
                  >
                    Muine Bay
                  </a>
                </div>
              </div>
            </div>
          );
        })}
    </>
  );
};

export default connect(state => {
  return {
    lang: state.lang.currentLang
  };
})(FourColumnsContainer);
