import React, { useEffect } from "react";
import { connect } from "react-redux";
import Banner from "../Common/Banner";
import NewsDetailContaier from "./NewsDetailContainer";
import { setNewsDetail as setNewsDetailAction } from "../../redux/actions/news";
import HotNewsContainer from "../News/HotNewsContainer";

const NewsDetail = ({ newsList, currentNews, lang, ...others }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const newsId = others.match.params && others.match.params.id;
  const selectedNews = newsList.find(currentNews => {
    return currentNews.id === newsId;
  });
  const bannerTitle = lang === "vn" ? "TIN TC" : "NEWS";
  if (!selectedNews) return null;

  return (
    <div>
      <Banner name={bannerTitle} />
      <div className="container content-area">
        <div className="middle-align">
          <NewsDetailContaier selectedNews={selectedNews} />
          <div id="sidebar">
            {/* <CategoriesContainer /> */}
            <HotNewsContainer hotItems={newsList} />
          </div>{" "}
          <div className="clear"></div>
        </div>
      </div>
    </div>
  );
};

export default connect(
  state => {
    return {
      news: state.muineNews.packageDetail,
      newsList: state.muineNews.list,
      lang: state.lang.currentLang
    };
  },
  dispatch => ({
    setNewsDetail: data => dispatch(setNewsDetailAction(data))
  })
)(NewsDetail);
