import React from "react";
import { connect } from "react-redux";

const RoomPhotosContainer = ({ rooms, lang }) => {
  const title = lang === "vn" ? "Hnh nh" : "Rooms Photo";
  let firstHalf = [];
  rooms.forEach((room, index) => {
    const urls = room && room.urls;
    if (index < rooms.length / 2) {
      firstHalf = firstHalf.concat(urls ?? []);
    }
  });
  return (
    <>
      <div className="section_head">
        <h2 className="section_title">
          {title}
          <span></span>
        </h2>
      </div>
      {firstHalf &&
        firstHalf.map((photoUrl, index) => {
          const isLast = index !== 0 && (index + 1) % 3 === 0;
          const classNameGallery = isLast
            ? "gallery gal-col-three lastcols"
            : "gallery gal-col-three";
          if (index >= 6) {
            return null;
          }
          return (
            <div className={classNameGallery}>
              <div className="gallery-image">
                <img alt=" " src={photoUrl} />
                <div className="gallery-icon">
                  <a
                    href={photoUrl}
                    data-fancybox="images"
                    className="yellow-text"
                  >
                    Muine Bay
                  </a>
                </div>
              </div>
            </div>
          );
        })}
    </>
  );
};

export default connect(state => {
  return {
    rooms: state.room.list,
    lang: state.lang.currentLang
  };
})(RoomPhotosContainer);
