import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { setServiceDetail as setServiceDetailAction } from "../../redux/actions/service";

const Services = ({ lang, services, setServiceDetail }) => {
  const servicesImg = [
    "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/services%2Fservice1.jpg?alt=media",
    "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/services%2Fservice4.jpg?alt=media",
    "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/services%2Fservice2.jpg?alt=media",
    "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/services%2Fservice3.jpg?alt=media",
    "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/services%2Fservice3.jpg?alt=media"
  ];

  const setDetail = data => {
    setServiceDetail(data);
  };

  return (
    <section className="no-padding">
      <div className="full-container">
        <div
          className="wow fadeInUp"
          data-wow-duration="1s"
          style={{
            visibility: "visible",
            animationDuration: "1s",
            animationName: "fadeInUp"
          }}
        >
          {services &&
            services.map((service, index) => {
              const isLast = index === 3;
              if (index > 3) {
                return null;
              }
              return (
                <div
                  key={index}
                  className={isLast ? "one_fourth last_column" : "one_fourth"}
                >
                  <div className="feature-box">
                    <div className="feature-box-thumb">
                      <img alt=" " src={servicesImg[index]} />
                      <div className="featture-box-cont">
                        <h2>{service.name[lang]}</h2>
                        <p> {service.short_description[lang]} </p>
                        <Link
                          style={{ color: "#CE9B51" }}
                          to={`/serviceDetail/${service.id}`}
                          onClick={() => setDetail(service.id)}
                        >
                          Read More
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          <div className="clear" />
        </div>
      </div>
    </section>
  );
};

export default connect(
  state => {
    return {
      service: state.muineService.serviceDetail,
      services: state.muineService.list,
      lang: state.lang.currentLang
    };
  },
  dispatch => ({
    setServiceDetail: data => dispatch(setServiceDetailAction(data))
  })
)(Services);
