const initState = {
  list: [],
  newsDetail: {}
};
function muineNewsReducer(state = initState, action) {
  if (action.type === "SET_NEWS") {
    return {
      ...state,
      list: action.data
    };
  }
  if (action.type === "SET_NEWS_DETAIL") {
    return {
      ...state,
      newsDetail: action.data
    };
  }

  return state;
}
export default muineNewsReducer;
