import React from "react";
import { connect } from "react-redux";
import { setRoomDetail as setRoomDetailAction } from "../../redux/actions/room";
import { Link } from "react-router-dom";

const Rooms = ({ dispatch, lang, rooms, setRoomDetail }) => {
  const setDetail = data => {
    setRoomDetail(data);
  };

  const roomInformation = rooms.find(room => {
    return room.id === "common";
  });

  const priceSubtitle = lang === "vn" ? "Phng/m" : "From/Per Night";
  return (
    <section style={{ backgroundColor: "#f6f6f6" }}>
      <div className="container">
        <div
          className="wow fadeInUp"
          data-wow-duration="1s"
          style={{
            visibility: "visible",
            animationDuration: "1s",
            animationName: "fadeInUp"
          }}
        >
          <div className="section_head">
            <h2 className="section_title">
              {roomInformation &&
                roomInformation.title &&
                roomInformation.title[lang]}
              <span />
            </h2>
            <p>
              {roomInformation &&
                roomInformation.subTitle &&
                roomInformation.subTitle[lang]}{" "}
            </p>
          </div>

          {rooms &&
            rooms.map((room, index) => {
              if (room.id === "common") {
                return null;
              }
              const isLast = index !== 0 && index % 3 === 0;
              return (
                <div
                  key={index}
                  className={isLast ? "one_third last_column" : "one_third"}
                >
                  <div className="hotel-room">
                    <div className="hotel-room-thumb">
                      <img
                        alt="MUINE BAY"
                        src={room && room.urls && room.urls[0]}
                      />
                      <div className="room-thumb-overlay">
                        <Link
                          to={`/rooms/${room.id}`}
                          onClick={() => setDetail(room.id)}
                        >
                          View More
                        </Link>
                      </div>
                    </div>
                    <div className="hotel-room-cont">
                      <div className="hotel-room-cont-wrap">
                        <Link
                          to={`/rooms/${room.id}`}
                          onClick={() => setDetail(room.id)}
                        >
                          <h3 className="yellow-text">
                            {room.name && room.name[lang]}
                          </h3>
                        </Link>
                        <p>
                          {" "}
                          {room.short_description &&
                            room.short_description[lang]}{" "}
                        </p>
                        <div className="hotel-room-price">
                          {room.price && room.price[lang]}
                          <span className="price-title">
                            {priceSubtitle}
                          </span>{" "}
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="clear" />
                </div>
              );
            })}

          {/* Next */}
          <div className="clear" />
        </div>
        <div className="clear" />
      </div>
    </section>
  );
};

export default connect(
  state => {
    return {
      lang: state.lang.currentLang,
      rooms: state.room.list
    };
  },
  dispatch => ({
    setRoomDetail: data => dispatch(setRoomDetailAction(data))
  })
)(Rooms);
